import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import methods from "../../../assets/img/methods.svg";

import styles from "assets/jss/material-kit-react/views/componentsSections/tabsStyle.js";
import Button from "../../../components/CustomButtons/Button";

const useStyles = makeStyles(styles);

export default function SectionTabsTwo() {
    const classes = useStyles();
    return (
        <div className={classes.section}>
            <div className={classes.container}>
                <h3>Mtodos de evaluacin de usabilidad</h3>
                <GridContainer>
                    <GridItem xs={12} sm={12} md={6}>
                        <p>Lla usabilidad no se debera de terminar de testear cuando se lanza el producto al mercado, sino que debera de consistir en un proceso iterativo, efectundose durante cualquier etapa del diseo para poder contribuir a la continua mejora de la experiencia del usuario. La tecnologa evoluciona y tambin lo hacen las formas en las que un usuario interacta con un determinado producto.</p>
                        <ul>
                            <li>La evaluacin heurstica</li>
                            <li>El recorrido cognitivo (cognitive walkthrough)</li>
                            <li>La combinacin de ambas tcnicas: el recorrido heurstico</li>
                        </ul>
                        <center>
                            <a href={"https://medium.com/@eliseohdez/m%C3%A9todos-y-t%C3%A9cnicas-de-evaluaci%C3%B3n-de-la-usabilidad-sin-personas-usuarias-e8f7b03c8654"} target={"blank"}> <Button color="primary" size="lg" simple> Ms informacin </Button></a>
                        </center>
                    </GridItem>
                    <GridItem xs={12} sm={12} md={6}>
                        <img src={methods} alt="usability" style={{width:"80%", height:"80%"}} />

                    </GridItem>
                </GridContainer>

            </div>
        </div>
    );
}
